body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  background: #7ea2d8;
}

.slider {
  max-height: 768px;
  max-width: 1024px;
  overflow: hidden;
  z-index: 5;
}

.slick-dots {
  width: 20px !important;
  display: flex !important;
  flex-direction: column !important;
  bottom: 50% !important;
  left: 95%;
}

.slick-dots li {
  margin-bottom:5px !important;
}

.slick-active .dots {
  background: #f78b1f !important;
}

.dots {
  border-radius: 50%;
  background: #fff !important;
  height: 15px !important;
  width: 15px !important;
}

.slick-vertical .slick-slide {
  border: none !important;
}

.buttonNext {
  position: relative;
  top: 721px;
  left: 25px;
  border: 0px;
  background: none;
  height: 40px;
  margin-bottom: -40px;
  width: 45px;
  z-index: 10;
  flex-wrap: nowrap;
  outline: 0;
}

.item {
    min-height: 768px !important;
    min-width: 1024px;
}
.slick-active div .itemHeader  {
    background:url(/onpoint_test/static/media/next.b8c54cea.png) center bottom/100% no-repeat, url(/onpoint_test/static/media/ContentHeaderBackground.1b15c3d4.png) no-repeat !important;
}

.itemHeader {
    background:url(/onpoint_test/static/media/ContentHeaderBackground.1b15c3d4.png) no-repeat !important;
    background-size: 1024px;
    font-family: 'Roboto', sans-serif;
    color: #0d319c;
}

.itemHeader * {
    position: relative;
}

.itemHeader h1 {
    font-size: 50px;
    font-weight: normal;
    left:75px;
    top: 120px;
    margin-top: 0px;
}

.itemHeader span {
    font-size: 20px;
    font-weight: normal;
    display: flex;
}

.targetOne {
    left:552px;
    top:97px;
    align-items: center;
}

.targetTwo, .targetThree, .targetFore {
    flex-direction: column-reverse;
}

.targetTwo {
    right:225px;
    top:165px;
    align-items: center;
}

.targetThree {
    left:318px;
    top:180px;
    align-items: center;
}

.targetFore {
    top:140px;
    right: 518px;
    align-items: flex-end;
}

.targetExternalCircle {
    margin: 10px;
    background: rgba(246, 139, 31, 0.22);
}

.targetInternalCircle {
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.targetExternalCircle, .targetInternalCircle::before, .targetInternalCircle::after {
    border-radius: 50%;
    border:1px solid rgba(222, 121, 27, 0.8);
    box-shadow: 0px 0px 0 1px rgba(208, 20, 55, 0.2);
}

.targetInternalCircle::before, .targetInternalCircle::after {
    position: absolute;
    opacity: 0;
    content: '';
    background: rgba(246, 139, 31, 0.3);
}

.targetInternalCircle::before {
    -webkit-animation: targetInternalCircle 2s infinite;
            animation: targetInternalCircle 2s infinite;
}

.targetInternalCircle::after {
    -webkit-animation: targetInternalCircle 2s 1s infinite ;
            animation: targetInternalCircle 2s 1s infinite ; 
}

@-webkit-keyframes targetInternalCircle {
    0% {
        opacity: 1;
        height: 2%;
        width: 2%;
    }
    100% {
        opacity: 1;
        height: 100%;
        width: 100%;
    }
}

@keyframes targetInternalCircle {
    0% {
        opacity: 1;
        height: 2%;
        width: 2%;
    }
    100% {
        opacity: 1;
        height: 100%;
        width: 100%;
    }
}
